import { KeyValuePipe } from '@angular/common';
import { Component, input } from '@angular/core';
import { RouterLink } from '@angular/router';

interface NavbarItem {
  [componente: string]: string;
}

@Component({
  selector: 'nav-iimas',
  imports: [RouterLink, KeyValuePipe],
  templateUrl: './nav-iimas.component.html',
  styleUrl: './nav-iimas.component.css',
})
export class NavIimasComponent {
  navInfo = input<NavbarItem>();
  navImg = input<string>();
}
